.gallery {
  margin-bottom: 1.5em; }

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%; }
  .gallery-columns-2 .gallery-item {
    max-width: 50%; }
  .gallery-columns-3 .gallery-item {
    max-width: 33.33%; }
  .gallery-columns-4 .gallery-item {
    max-width: 25%; }
  .gallery-columns-5 .gallery-item {
    max-width: 20%; }
  .gallery-columns-6 .gallery-item {
    max-width: 16.66%; }
  .gallery-columns-7 .gallery-item {
    max-width: 14.28%; }
  .gallery-columns-8 .gallery-item {
    max-width: 12.5%; }
  .gallery-columns-9 .gallery-item {
    max-width: 11.11%; }

.gallery-caption {
  display: block; }

body {
  min-width: 320px; }
  body > .site {
    overflow: hidden; }

.wrapper {
  padding: 0; }

.social {
  list-style: none;
  margin: 0 -5px;
  padding: 0; }
  .social li {
    margin: 5px;
    display: inline-block;
    line-height: 1; }

.btn,
.wpcf7 input[type=submit], .gform_button, .is-style-standard-button .wp-block-button__link, .is-style-outline-button .wp-block-button__link {
  text-decoration: none; }
  .btn i, .wpcf7 input[type=submit] i, .gform_button i, .is-style-standard-button .wp-block-button__link i, .is-style-outline-button .wp-block-button__link i {
    vertical-align: middle; }

ul.menu {
  list-style: none;
  margin: 0;
  padding: 0; }
  ul.menu.horizontal li {
    display: inline-block; }

.entry-content .alignwide {
  margin-left: -80px;
